import { call, put } from 'redux-saga/effects'
import api from '../../../services'
import * as actions from './actions'


export function* loadShow() {
  try {
    const response = yield call(api.get, 'lista')

    yield put(actions.loadShowSuccess(response.data))
  } catch (err) {
    yield put(actions.loadShowFailure())
  }
}

export function* createShow({ payload }) {
  try {
    yield call(api.post, 'lista', payload.data)
    const response = yield call(api.get, "lista")
    yield put(actions.createShowSuccess(response.data))
  } catch (err) {
    yield put(actions.createShowFailure())
  }
}

export function* updateShow({ payload }) {
  console.log('opa carai', payload)
  try {
    yield call(api.put, `lista/${payload.data.id}`, payload.data)
    const response = yield call(api.get, "lista")
    yield put(actions.updateShowSuccess(response.data))
  } catch (err) {
    yield put(actions.updateShowFailure())
  }
}

export function* deleteShow({ payload }) {
  try {
    yield call(api.delete, `lista/${payload.data}`)
    const response = yield call(api.get, "lista")
    yield put(actions.deleteShowSuccess(response.data))
  } catch (err) {
    yield put(actions.deleteShowFailure())
  }
}