import React, { useRef, useState } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Card, CardBody, ButtonToolbar, ButtonGroup } from "reactstrap";

function Report({
  title,
  data,
  columns,
  tableRef,
  onRowClicked,
  headerRightComponent,
  visibleColumns,
  ...rest
}) {
  const table = useRef(null);
  const [rColumns] = useState(columns);

  const renderHeaderRightComponent = () => {
    if (headerRightComponent) {
      if (!visibleColumns || !visibleColumns.length)
        return headerRightComponent;
    }

    return (
      <div
        style={{
          alignItems: "center",
          display: "flex"
        }}
      >
        <ButtonToolbar>
          <ButtonGroup className="btn-group--icons" dir="ltr">
            {headerRightComponent}
          </ButtonGroup>
        </ButtonToolbar>
      </div>
    );
  };

  return (
    <Card>
      <CardBody style={{ padding: 0 }}>
        <div
          className="card__title"
          style={{
            marginBottom: 0,
            padding: 16,
            justifyContent: "space-between"
          }}
        >
          <h5 className="bold-text">{title || "Relatório"}</h5>
          <div style={{ display: "flex", alignItems: "flex-end" }}>
            {renderHeaderRightComponent()}
          </div>
        </div>
        <ReactTable
          className="-striped -highlight dark"
          data={data}
          ref={tableRef || table}
          columns={columns}
          defaultPageSize={15}
          pageSizeOptions={[5, 10, 15, 20, 25, 50, 100]}
          getTdProps={(state, rowInfo, column) => {
            return {
              style: {
                display: "flex",
                alignItems: "center",
                cursor:
                  onRowClicked && !(column && column.id === "actions")
                    ? "pointer"
                    : undefined
              },
              onClick: (e, handleOriginal) => {
                if (rowInfo && !(column && column.id === "actions")) {
                  if (onRowClicked) {
                    onRowClicked(rowInfo.original);
                  } else {
                    handleOriginal && handleOriginal(e);
                  }
                }
              }
            };
          }}
          {...rest}
        />
      </CardBody>
    </Card>
  );
}


export default Report;