import React, {
  createContext,
  useCallback,
  useContext, useEffect, useState
} from "react";
import { app } from "../firebase_config";
import api from "../services";


const AuthContext = createContext({});

const AuthProvider = ({ children }) => {
  const [token, setToken] = useState(() => {
    const token = localStorage.getItem("@PermissionYT:token");

    if (token) {
      api.defaults.headers.authorization = `Bearer ${token}`;

      return token;
    }

    return null
  });
  const [user, setUser] = useState(null);
  const [loadingAuthState, setLoadingAuthState] = useState(true);
  const [tokenFirebase, setTokenFirebase] = useState('');

  useEffect(() => {
    app.auth().onAuthStateChanged((user) => {
      console.log('usuario da merda', user);
      setUser(user);
      getTokenFirebase()
      setLoadingAuthState(false);
    });
  }, [user]);

  useEffect(() => {
    if (token) {
      localStorage.setItem("@PermissionYT:token", token);
      api.defaults.headers.authorization = `Bearer ${token}`;
    }
  }, [token]);

  const userLogged = useCallback(() => {
    const tk = localStorage.getItem("@PermissionYT:token");
    console.log('tokeeeeen', tk);
    if (tk) {
      return true;
    }
    return false;
  }, []);

  const getTokenFirebase = () => {
    if (user) {
      app.auth().currentUser.getIdToken().then(res => {
        console.log('firebaseToken', res)
        // tokenApi(res)
        setToken(res)
        localStorage.setItem("@PermissionYT:token", res);
        api.defaults.headers.authorization = `Bearer ${res}`;
      })
        .catch(error => {
          console.log(error.message);
        });
    }
  }

  const tokenApi = (res) => {
    api.post('/auth', null, {
      headers: {
        webstage_auth: res
      }
    }).then(token => {
      setToken(token)

    }).catch(err => {

    })

  }


  const signOut = () => {
    const token = localStorage.getItem('@PermissionYT:token')
    if (token) {
      app.auth().signOut()
      localStorage.clear()
    }
  }



  return (
    <AuthContext.Provider value={
      {
        user,
        setUser,
        loadingAuthState,
        userLogged,
        signOut,
        token
      }
    }>
      {children}
    </AuthContext.Provider>
  );
};

function useAuth() {
  const context = useContext(AuthContext);
  return context;
}

export { AuthProvider, useAuth };
