import { Reducer } from 'redux'
import {
  SHOWS_LOAD,
  SHOWS_LOAD_SUCCESS,
  SHOWS_LOAD_ERROR,
  SET_VALUE,
  SHOWS_CREATE,
  SHOWS_SUCCESS,
  SHOWS_ERROR,
  SHOWS_UPDATE,
  SHOWS_UPDATE_SUCCESS,
  SHOWS_UPDATE_ERROR,
  SHOWS_DELETE,
  SHOWS_DELETE_SUCCESS,
  SHOWS_DELETE_ERROR
} from './types'

const INITIAL_STATE = {
  data: [],
  showSelected: null,
  error: false,
  loading: false,
  showModalDelete: false,
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case SHOWS_LOAD:
      return { ...state, loading: true}
    case SHOWS_LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        data: action.payload.data,
      }
    case SHOWS_LOAD_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
      }

    case SET_VALUE:
      return { ...state, ...action.payload.payload }

    default:
      return state

    case SHOWS_CREATE:
      return { ...state, loading: true}
    case SHOWS_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        data: action.payload.data,
      }
    case SHOWS_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
      }
    
    case SHOWS_UPDATE:
      return { ...state, loading: true}
    case SHOWS_UPDATE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        data: action.payload.data,
      }
    case SHOWS_UPDATE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
      }

    case SHOWS_DELETE:
      return { ...state, loading: true}
    case SHOWS_DELETE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        data: action.payload.data,
      }
    case SHOWS_DELETE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
      }
  }
}

export default reducer
