import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { app } from '../../firebase_config';
import { useAuth } from '../../_contexts/auth';
import { useToast } from '../../_contexts/toast';
import CreateForm from './components/CreateForm';

function Login() {
  const dispatch = useDispatch();
  const { setUser, setToken, user } = useAuth();
  // const { data, loading, error } = useSelector(s => s.auth)
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('')
  const { showToast } = useToast();
  const history = useHistory();


  const onSubmit = async data => {
    // const errors = validate(data);

    // if (Object.keys(errors).length > 0) {
    // 	throw new SubmissionError(errors);
    // }
    // const values = {
    //   email: data.email,
    //   password: data.password,
    // }
    setLoading(true)
    app
      .auth()
      .signInWithEmailAndPassword(data.email, data.password)
      .then(user => {
        setUser(user);
        showToast('success', 'Login efetuado com sucesso');
        setLoading(false)
        console.log(user, 'res')
        history.push('/dashboard')

      })
      .catch(error => {
        console.log(error.message);
        setMessage(JSON.stringify(error))
        setLoading(false)
        showToast('error', 'Ooops verifique suas credenciais ' + error.message)
      });

    showToast('error', message)
    // try {
    // const response = await signIn(values);
    // if (!error) {
    //   setToken(data?.accessToken)

    //   history.push('/dashboard');
    // } else {

    // }
    // } catch (error) {

    // }
    // setLoading(false)

  };

  return (
    <div className="d-flex align-items-center auth px-0 lock-full-bg">
      <div className="row w-100 mx-0">
        <div className="col-md-6 col-lg-4 mx-auto">
          <div className="auth-form-transparent text-left py-5 px-4 px-sm-5">
            {/* <div className="brand-logo">
              <img src={require("../../assets/images/logo-dark.svg")} alt="logo" />
            </div> */}
            {/* <h4 className="font-weight-light">Palco Web Admin</h4> */}
            {/* <h3 className="font-weight-light">Login</h3> */}

            <CreateForm
              onSubmit={onSubmit}
              loading={loading}
            />

          </div>
        </div>
      </div>
    </div>
  );
}

export default Login
