import { app } from '../../../firebase_config';
import { formatUsers } from '../../../helpers/dtos/users';
import * as actions from './actions'
import * as types from './types'


export function setValue(payload) {
    return (dispatch) => {
        dispatch({
            type: types.SET_VALUE
        });
    };
}

export const getUsers = () => async (dispatch) => {
    dispatch({
        type: types.LOAD_USER
    });
    try {
        const res = app.firestore().collection('users').get()
        let users = [];
        res.forEach(user => {
            users.push({ id: user.id, ...user.data() })
        });
        const user = formatUsers(users)

        dispatch({
            type: types.LOAD_USER_SUCCESS,
            payload: user

        });

    } catch (err) {
        console.log(err);
        return dispatch({
            type: types.LOAD_USER_ERROR
        });
    }
};

