export const signupUser = (userDetails) => {
    //deconstruct the users details we will need these later
    return async (dispatch, getState) => {
        dispatch(actions.createUserRequest());
        const { fullName, profileId, birthday, email, password } = userDetails;
        return () => {
            //user firebase using the appropriate firebase method
            app.auth().createUserWithEmailAndPassword(email, password)
                .then(() => {
                    //Once the user creation has happened successfully, we can add the currentUser into firestore
                    //with the appropriate details.
                    app.firestore().collection('users').doc(app.auth().currentUser.uid)
                        .set({
                            fullName,
                            birthday,
                            profileId
                        })
                    //ensure we catch any errors at this stage to advise us if something does go wrong
                    dispatch(actions.createUserSuccess())
                        .catch(error => {
                            console.log('Something went wrong with added user to firestore: ', error);
                            dispatch(actions.loadUserFailure())
                        })
                }).catch(error => {
                    console.log('Something went wrong with sign up: ', error);
                    dispatch(actions.loadUserFailure())
                })
        }
    }
};
