import React from 'react'
import { Button, Modal } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux';
import { setValue, deleteShow  } from '../../../store/ducks/shows/actions';

export default function ModalDelete() {
    const dispatch = useDispatch();
    const {showModalDelete, showSelected, loading} = useSelector((state) => state.shows);
    const handleClose = () => {
        dispatch(setValue({showModalDelete: false}));
    }
    const handleDelete = () => {
        dispatch(deleteShow(showSelected.id));
        handleClose()
    }

    return (
        <div>
           <Modal show={showModalDelete} onHide={handleClose}>
                <Modal.Header>Deletar Conteúdo</Modal.Header>
                <Modal.Body>Tem certeza que deseja excluir o conteúdo?</Modal.Body>
                <Modal.Footer>
                <Button variant="danger" type="button" disabled={loading} onClick={handleDelete}>{loading?"...":"Enviar"}</Button>
                <Button variant="secondary" type="button" disabled={loading} onClick={handleClose}>Fechar</Button>
                </Modal.Footer>
            </Modal> 
        </div>
    )
}
