import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { createShow, updateShow } from '../../store/ducks/shows/actions';
import CreateForm from './components/CreateForm';



function CreateShow() {
  const {id} = useParams();
  const history = useHistory();
  const dispatch = useDispatch();
  const { loading, error, data, showSelected} = useSelector((state) => state.shows);
  const [message, setMessage] = useState({})
  const [close, setClose] = useState(false)
  console.log(data);
  const onSubmit = data => {
		// const errors = validate(data);

		// if (Object.keys(errors).length > 0) {
		// 	throw new SubmissionError(errors);
		// }
    const values = {
        title: data.title,
        synopsis: data.synopsis,      
        link: data.link, 
        artista: data.artista, 
        categoria: data.categoria,        
        classificacao: data.classificacao,   
        descricaoClassifica: data.descricaoClassifica,
        elenco: data.elenco,
        producao: data.producao,         
        direcao: data.direcao,
        duracao:data.duracao,
        videoID: data.videoID,
        videoID2: data.videoID2,     
    }
    // const resp = api.post('/sessions', values)
    // console.log(resp);
    const resp = {id, ...values}
    console.log('qq coisa', resp)

    if(!id) {
      console.log('cheguei no if')
      dispatch(createShow(values))
      history.push("/shows/load");
    } else {
      console.log('cheguei no else')
      dispatch(updateShow({id, ...values}))
      history.push("/shows/load");
    }
      
    if(error){
      setClose(true)
      setMessage({code: 'Error', message: "Oooops algo deu errado :("})
    }else{
      setClose(true)
      setMessage({code: 'Ok', message: "Usuário criado com sucesso"})
    }

	};
  const initialValues = id? {
    title: showSelected.title,
    synopsis: showSelected.synopsis,      
    link: showSelected.link, 
    artista: showSelected.artista, 
    categoria: showSelected.categoria,        
    classificacao: showSelected.classificacao,   
    descricaoClassifica: showSelected.descricaoClassifica,
    elenco: showSelected.elenco,
    producao: showSelected.producao,         
    direcao: showSelected.direcao,
    duracao:showSelected.duracao,
    videoID: showSelected.videoID,
    videoID2: showSelected.videoID2,     
  }:""

  return (
    <div>
    <div className="d-flex align-items-center auth px-0">
      <div className="row w-100 mx-0">
        <div className="col-lg-4 mx-auto">
          <div className="auth-form-light text-left py-5 px-4 px-sm-5">
            <div className="brand-logo">
              {/* <img src={require("../../assets/images/logo-dark.svg")} alt="logo" /> */}
            </div>
            <h4>Hello! let's get started</h4>
            <h6 className="font-weight-light">Sign in to continue.</h6>

            <CreateForm
              onSubmit={onSubmit}
              loading={loading}
              initialValues={initialValues}
            />
 
          </div>
        </div>
      </div>
    </div>  
  </div>
  );
}

export default CreateShow
