import { all, takeLatest } from 'redux-saga/effects'
import { createShow, deleteShow, loadShow, updateShow } from './shows/sagas'
import { SHOWS_CREATE, SHOWS_DELETE, SHOWS_LOAD, SHOWS_UPDATE } from './shows/types'

export default function* rootSaga() {
  return yield all([

    // takeLatest(CREATE_USER, createUser),
    // takeLatest(LOAD_USER, loadUser)

    takeLatest(SHOWS_LOAD, loadShow),
    takeLatest(SHOWS_CREATE, createShow),
    takeLatest(SHOWS_DELETE, deleteShow),
    takeLatest(SHOWS_UPDATE, updateShow),
  ])
}
