import React, { useEffect, useState } from 'react';

import Navbar from './Navbar';
import Sidebar from './Sidebar';
import SettingsPanel from './SettingsPanel';
import Footer from './Footer';
import { useAuth } from '../_contexts/auth';

function Layout({ children, history }) {
  const [isFullPageLayout, setIsFullPageLayout] = useState(false);
  const { signOut } = useAuth();

  useEffect(() => {
    console.log(history);
    onRouteChanged();
  }, [history.location])

  function onRouteChanged() {
    console.log("ROUTE CHANGED");
    const body = document.querySelector('body');
    if (history.location.pathname === '/layout/RtlLayout') {
      body.classList.add('rtl');
    }
    else {
      body.classList.remove('rtl')
    }
    window.scrollTo(0, 0);
    // const fullPageLayoutRoutes = ['/', '/register', '/dashboard', '/users', '/users/create', '/users/edit'];
    // for (let i = 0; i < fullPageLayoutRoutes.length; i++) {
    //   if (history.location.pathname === fullPageLayoutRoutes[i]) {
    setIsFullPageLayout(true)

    document.querySelector('.page-body-wrapper').classList.add('full-page-wrapper');
    //     break;
    //   } else {
    //     setIsFullPageLayout(false)

    //     document.querySelector('.page-body-wrapper').classList.remove('full-page-wrapper');
    //   }
    // }
  }

  return (
    <div className="container-scroller">
      {isFullPageLayout && <Navbar />}
      <div className="container-fluid page-body-wrapper">
        {isFullPageLayout && <Sidebar location={history.location} signOut={signOut} />}
        <div className="main-panel">
          <div className="content-wrapper">
            {children}
            {isFullPageLayout && <SettingsPanel />}
          </div>
          {isFullPageLayout && <Footer />}
        </div>
      </div>
    </div>
  );
}

export default Layout;