import React from 'react';
import { Button, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Field, reduxForm } from "redux-form";
import InputCustom from '../../../components/Input';
import Spinner from '../../../Layout/Spinner';

function CreateForm({
    handleSubmit,
	reset,
	loading,
    initialValues
}) {
  return (
    <Form className="pt-3" onSubmit={handleSubmit}>
     <Form.Group className="d-flex search-field">
      {/* <Form.Control type="email" placeholder="Username" size="lg" className="h-auto" /> */}
      <Field
        name="id"
        component='input'
        className="form-control"
        type="text"
        placeholder="ID"
	 />
    </Form.Group>
    <Form.Group className="d-flex search-field">
      {/* <Form.Control type="email" placeholder="Username" size="lg" className="h-auto" /> */}
      <Field
        name="title"
        component='input'
        className="form-control"
        type="text"
        placeholder="Título"
	 />
    </Form.Group>
    <Form.Group className="d-flex search-field">
      
      <Field
        name="synopsis"
        component='input'
        className="form-control"
        type="text"
        placeholder="Sinopse"
	 />
    </Form.Group>
    <Form.Group>
    <Field
        name="link"
        component='input'
        className="form-control"
        type="text"
        placeholder="Link"
	 />
    </Form.Group>
    <Form.Group className="d-flex search-field">
        <Field
        name="artista"
        component='input'
        className="form-control"
        type="text"
        placeholder="Produtora"
	 />
    </Form.Group>
    <Form.Group>
    <Field
    name="categoria"
    component='input'
    className="form-control"
    type="text"
    placeholder="Categoria"
 />
</Form.Group>
<Form.Group className="d-flex search-field">
  
  <Field
    name="classificacao"
    component='input'
    className="form-control"
    type="text"
    placeholder="Classificação"
 />
</Form.Group>
<Form.Group>
<Field
        name="descricaoClassifica"
        component='input'
        className="form-control"
        type="text"
        placeholder="Descrição da Classificação"
	 />
    </Form.Group>
    <Form.Group className="d-flex search-field">
      
      <Field
        name="elenco"
        component='input'
        className="form-control"
        type="text"
        placeholder="Elenco"
	 />
    </Form.Group>
    <Form.Group>
    <Field
        name="producao"
        component='input'
        className="form-control"
        type="text"
        placeholder="produção"
	 />
    </Form.Group>
    <Form.Group className="d-flex search-field">
      
      <Field
        name="duracao"
        component='input'
        className="form-control"
        type="text"
        placeholder="duração"
	 />
    </Form.Group>
     <Form.Group className="d-flex search-field">
     
     <Field
       name="videoID"
       component='input'
       className="form-control"
       type="text"
       placeholder="videoID"
    />
   </Form.Group>
    <Form.Group className="d-flex search-field">
    
    <Field
      name="videoID2"
      component='input'
      className="form-control"
      type="text"
      placeholder="videoID2"
   />
  </Form.Group>

    
    <div className="mt-3">
      {/* <Link className="btn btn-block btn-primary btn-lg font-weight-medium auth-form-btn" to="/dashboard">SIGN IN</Link> */}
      <Button className="btn btn-block btn-primary btn-lg font-weight-medium auth-form-btn" type="submit" disabled={loading}>
					{loading ? (
						'...'
					) : (
						"LOGIN"
					)}
				</Button>
    </div>
    <div className="my-2 d-flex justify-content-between align-items-center">
      <div className="form-check">
        <label className="form-check-label text-muted">
          <input type="checkbox" className="form-check-input"/>
          <i className="input-helper"></i>
          Keep me signed in
        </label>
      </div>
      <a href="!#" onClick={event => event.preventDefault()} className="auth-link text-black">Forgot password?</a>
    </div>
    <div className="mb-2">
      <button type="button" className="btn btn-block btn-facebook auth-form-btn">
        <i className="mdi mdi-facebook mr-2"></i>Connect using facebook
      </button>
    </div>
    <div className="text-center mt-4 font-weight-light">
      Don't have an account? <Link to="/user-pages/register" className="text-primary">Create</Link>
    </div>
  </Form>
  );
}

export default reduxForm({
	form: "form_create_show",
	// validate
})(CreateForm);