import _ from "lodash";
import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Col, Container, Row } from "reactstrap";
import ReportTable from "../../components/Report";
import TableCustom from "../../components/Table";
import { app } from "../../firebase_config";
import { formatUsers } from "../../helpers/dtos/users";
import profiles from "../../helpers/models/profiles";
import { loadUserRequest, setValue } from "../../store/ducks/users/actions";
import { getUsers, getUsersFirestore } from "../../store/ducks/users/creators";
import { MdOutlineDeleteOutline, MdOutlineEdit } from 'react-icons/md'
import DeleteForm from "./components/DeleteForm";
import { Button, ButtonGroup } from "react-bootstrap";
import { useHistory } from "react-router";
import HeaderList from "./components/HeaderList";

let fetchDataDebounced = null;

function Report({ history }) {
	const dispatch = useDispatch();
	const nav = useHistory();
	// const { data, loading } = useSelector((state) => state.users)
	const [state, setState] = useState([])
	const [loading, setLoading] = useState(false)
	const columns =
		[
			{
				Header: "Nome",
				accessor: "fullName"
			},
			{
				Header: "Email",
				id: "email",
				sortable: true,
				accessor: "email"
			},
			{
				Header: "Telefone",
				sortable: false,
				accessor: "phone"
			},
			{
				Header: "Perfil",
				id: "profile",
				sortable: false,
				accessor: "profile"
			},
			{
				Header: 'Ações',
				cell: (client) => {
					return (
						<ButtonGroup>
							<Button
								variant="secondary"

								onClick={() => handleEdit(client)}
								className="d-flex font-weight-bold text-white btn btn-xs"
							>
								<MdOutlineEdit className="text-white font-weight-bold mr-1" />
								<span className="text-white font-weight-bold mr-1">
									Editar
								</span>
							</Button>
							<Button
								variant="warning"
								onClick={() => handleDelete(client)}
								className="d-flex text-black font-weight-bold btn btn-xs"
							>
								<MdOutlineDeleteOutline className="text-black font-weight-bold mr-1" />
								<span className="text-black font-weight-bold mr-1">
									Excluir
								</span>
							</Button>
						</ButtonGroup>
					);
				},
			},
		]


	useEffect(() => {

		getUsers()
	}, [])

	const getUsers = async () => {
		setLoading(true)
		try {
			const res = await app.firestore().collection('users').get()
			let users = [];
			res.forEach(user => {
				users.push({ id: user.id, ...user.data() })
			});
			const user = formatUsers(users)
			setState(user)
			setLoading(false)


		} catch (err) {
			setLoading(false)
			console.log(err);
		}
	};


	const handleDelete = (user) => {
		dispatch(setValue({ showModalDelete: true, userSelected: user }));
	}


	const handleEdit = (user) => {

		dispatch(setValue({ userSelected: user }));
		nav.push(`/users/edit/${user.id}`)
	}

	return (
		<div className="dashboard">
			<Row>
				<Col md={12} class="table-responsive col-md-12">
					<HeaderList />

					<TableCustom
						title="Usuários"
						data={state}
						loading={loading}
						columns={columns}
					/>
				</Col>
			</Row>
			<DeleteForm />
		</div>

	);
}

export default Report;