import React from 'react';
import { Button, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Field, reduxForm } from "redux-form";
import InputCustom from '../../../components/Input';
import { DATE, PHONE } from '../../../components/NumberFormat';
import renderSelectField from '../../../components/Select';
import Spinner from '../../../Layout/Spinner';

function CreateForm({
  handleSubmit,
  reset,
  loading,
  initialValues,
  profilesOptions
}) {
  return (
    <Form autoComplete="new-off" className="pt-3" onSubmit={handleSubmit} >
      <Form.Row>
        <Form.Group className="col-md-6">
          <label for="email">Email</label>
          <Field
            id="email"
            name="email"
            component={InputCustom}
            className="form-control"
            type="text"
            placeholder="email"
          />
        </Form.Group>
        <Form.Group className="col-md-6">
          <label for="password">Senha</label>
          <Field
            id="password"
            name="password"
            component={InputCustom}
            className="form-control"
            type="password"
            placeholder="SENHA"
          />
        </Form.Group>
      </Form.Row>
      <Form.Row>
        <Form.Group className="col-md-5">
          <label for="fullName">Nome</label>
          <Field
            id="fullName"
            name="fullName"
            component={InputCustom}
            className="form-control "
            type="text"
            placeholder="NOME COMPLETO"
          />
        </Form.Group>
        <Form.Group className="col-md-4">
          <label for="phone">Telefone</label>
          <Field
            id="phone"
            name="phone"
            component={PHONE}
            className="form-control"
            type="text"
            placeholder="TELEFONE"
          />
        </Form.Group>
        <Form.Group className="col-md-3">
          <label for="birthday">Data de Aniversário</label>
          <Field
            id="birthday"
            name="birthday"
            component={DATE}
            className="form-control"
            type="text"
            placeholder="DATA DE ANIVERSÁRIO"
          />
        </Form.Group>
      </Form.Row>
      <Form.Row>
        <Form.Group className="col-md-12">
          <label for="profile">Perfil</label>
          <Field
            id="profile"
            name="profile"
            component={renderSelectField}
            className="form-control"
            type="text"
            placeholder="SELECIONE UM PERFIL"
            options={profilesOptions}
          />
        </Form.Group>
      </Form.Row>
      <div className="mt-3">
        <Button className="btn btn-block btn-primary btn-lg font-weight-medium" type="submit" disabled={loading}>
          {loading ? (
            <Spinner />
          ) : (
            "Cadastrar"
          )}
        </Button>
      </div>
    </Form>
  );
}

export default reduxForm({
  form: "form_create_user",
  // validate
})(CreateForm);