import React from 'react';
import { Col, Row, Spinner, Table, Container } from 'reactstrap';

function TableCustom({
    data,
    noData,
    loading,
    columns,
    title,
}) {
    return (
        data && (
            <div id="list" class="row">
                <div className="table-responsive col-md-12">
                    <Table striped dark>
                        <tr>
                            {
                                columns.map(col => (
                                    <th>{col.Header}</th>
                                ))
                            }
                        </tr>


                        <tbody className="bg-white text-black">

                            {data.length > 0 && !loading &&
                                data.map(row => (
                                    <tr>
                                        {
                                            columns.map((col, pos) => (
                                                <>
                                                    {
                                                        col.accessor ? (
                                                            <td key={`cell-${String(pos)}`}>
                                                                {row[col.accessor]}
                                                            </td>
                                                        ) :
                                                            (
                                                                <td key={`cell-${String(pos)}`}>
                                                                    {col.cell(row)}
                                                                </td>
                                                            )
                                                    }
                                                </>
                                            ))
                                        }
                                    </tr>
                                ))}

                        </tbody>

                    </Table>
                    {data.length === 0 && !loading
                        ? (
                            <p>{noData}</p> || <p>Nenhum registro encontrado</p>
                        ) : loading && (
                            <div className="spinner-wrapper">
                                <Spinner />
                            </div>
                        )}

                </div>
            </div>
        )
    );
}

export default TableCustom;