import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { app } from '../../firebase_config';
import profiles from '../../helpers/models/profiles';
import { useToast } from '../../_contexts/toast';
import CreateForm from './components/CreateForm';

function CreateUsers() {
  const dispatch = useDispatch();
  // const { loading, error } = useSelector(s => s.users)
  const [error, setError] = useState([])
  const [loading, setLoading] = useState(false)
  const { showToast } = useToast();
  const history = useHistory();


  const onSubmit = async data => {
    // const errors = validate(data);

    // if (Object.keys(errors).length > 0) {
    // 	throw new SubmissionError(errors);
    // }
    const values = {
      birthday: data.birthday,
      email: data.email,
      fullName: data.fullName,
      password: data.password,
      phone: data.phone,
      profileId: data.profile?.value
    }
    signupUser(values)



  };

  const signupUser = (userDetails) => {
    setLoading(true)
    const { fullName, profileId, birthday, email, password } = userDetails;
    app.auth().createUserWithEmailAndPassword(email, password)
      .then(() => {
        app.firestore().collection('users').doc(app.auth().currentUser.uid)
          .set({
            email,
            fullName,
            birthday,
            profileId
          })
          .catch(error => {
            console.log('Something went wrong with added user to firestore: ', error);
            setLoading(false)
            setError(error)
            showToast('error', 'Ooops algo deu errado ')

          })
        setLoading(false)
        showToast('success', 'Cadastro efetuado com sucesso');
      }).catch(error => {
        console.log('Something went wrong with sign up: ', error);
        setLoading(false)
        setError(error)
        showToast('error', 'Ooops algo deu errado ')

      })
  };


  const profilesOptions = Object.keys(profiles).map(profile => (
    { value: profile, label: profiles[profile] }
  ))

  return (
    <div className="dashboard align-items-center px-0 lock-full-bg w-80 h-80">
      <div className="row w-100 mx-0">
        <div className="col-md-12 col-lg-12 mx-auto">
          <div class="container">
            <h1 class="display-4 text-white">Cadastrar Usuário</h1>
          </div>
          <CreateForm
            onSubmit={onSubmit}
            loading={loading.create}
            profilesOptions={profilesOptions}
          />
        </div>
      </div>
    </div>
  );
}

export default CreateUsers
