import React, { PureComponent } from "react";
import Select from "react-select";

class SelectField extends PureComponent {

	handleChange = selectedOption => {
		const { onChange } = this.props;
		onChange(selectedOption);
	};

	render() {
		const { value, name, placeholder, options } = this.props;

		const customStyles = {
			option: (provided, state) => ({
				...provided,
				color: state.isSelected ? 'white' : 'black',
				background: state.isSelected ? '#202020' : 'white',
				padding: 20,
			}),
			control: (base, state) => ({
				...base,
				height: 46,
				borderRadius: 3,
				background: 'transparent',

			}),
			singleValue: (provided, state) => ({
				...provided,
				color: 'white',
				transition: 'all 300ms',
				fontWeigth: 600
			}),
		};

		return (
			<Select
				name={name}
				value={value}
				onChange={this.handleChange}
				options={options}
				clearable={false}
				className="react-select"
				placeholder={placeholder}
				classNamePrefix="react-select"
				styles={customStyles}
			/>
		);
	}
}

const renderSelectField = props => {
	const { input, meta, options, placeholder, className } = props;
	return (
		<div className={`form-group`}>
			<SelectField {...input} options={options} placeholder={placeholder} className={`${className}`} />
			{meta.touched && meta.error && (
				<span className="form__form-group-error">{meta.error}</span>
			)}
		</div>
	);
};


export default renderSelectField;