import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { loadShow, setValue } from '../../store/ducks/shows/actions';
import ModalDelete from './components/ModalDelete';


function LoadShow() {
  const history = useHistory();
  const dispatch = useDispatch();
  const { data, error, loading } = useSelector((state) => state.shows);
  const [show, setShow] = useState(false);
  const [id, setId] = useState('');

  const handleShowDelete = (show) => {
    dispatch(setValue({ showModalDelete: true, showSelected: show }));
  }

  const handleUpdate = (show) => {
    history.push(`/shows/update/${show.id}`)
    dispatch(setValue({ showSelected: show }));
  }



  useEffect(() => {
    dispatch(loadShow())
  }, []);

  const myStyle = {
    width: '400px',
    borderRadius: '4px',
    marginRight: '5px',
    marginBottom: '10px'
  }

  let itensToRender
  if (data) {
    itensToRender = data.map(d =>
      <form >
        <select style={myStyle}  >
          <option key={d.id} >Título: {d.title}</option>
          <option key={d.id} >Produtora: {d.Artista}</option>
          <option key={d.id} >Categoria: {d.categoria}</option>
          <option key={d.id} >Classificação: {d.classificacao}</option>
        </select>
        <button type="button" style={{ borderRadius: "4px", marginRight: '5px' }} onClick={() => handleUpdate(d)} > Editar</button>
        <button type="button" style={{ borderRadius: "4px", marginRight: '5px' }} onClick={() => handleShowDelete(d)} > Delete</button>
      </form>)

  }

  return (

    <div>
      {itensToRender}
      <ModalDelete />
    </div>

  );
}

export default LoadShow
