import React from "react";
import { Route, Switch, useHistory } from "react-router-dom";
import Layout from "../Layout";
import Login from "../pages/Account/Login";
import Dashboard from "../pages/Dashboard";
import CreateShow from "../pages/Show/CreateShow";
import LoadShow from "../pages/Show/Report";
import CreateUsers from "../pages/Users/CreateUsers";
import EditUsers from "../pages/Users/EditUsers";
import Report from "../pages/Users/Report";
import PrivateRoutes from "./PrivateRoutes";

const Routes = () => {
  const history = useHistory();
  return (
    <Switch>
      <Route path="/" exact component={Login} />
      <Layout history={history}>
        <PrivateRoutes exact path="/users" component={Report} />
        <PrivateRoutes path="/users/edit" component={EditUsers} />
        <PrivateRoutes path="/users/create" component={CreateUsers} />

        <PrivateRoutes exact path="/shows/load" component={LoadShow} />
        <PrivateRoutes path="/shows/create" component={CreateShow} />
        <PrivateRoutes path={`/shows/update/:id`} component={CreateShow} />

        {/* registrar as rotas no navbar */}
        <PrivateRoutes exact path="/dashboard" component={Dashboard} />
        {/* <Route path="/dashboard" component={Users} /> */}
      </Layout>

      {/* <PrivateRoutes
        path="/list"
        component={List}
        role="ROLE_ADMIN,ROLE_USER"
      /> */}
    </Switch>
  );
};

export default Routes;