import { action } from 'typesafe-actions'
import {
  SHOWS_LOAD,
  SHOWS_LOAD_SUCCESS,
  SHOWS_LOAD_ERROR,
  SET_VALUE,
  SHOWS_CREATE,
  SHOWS_SUCCESS,
  SHOWS_ERROR,
  SHOWS_UPDATE,
  SHOWS_UPDATE_SUCCESS,
  SHOWS_UPDATE_ERROR,
  SHOWS_DELETE,
  SHOWS_DELETE_SUCCESS,
  SHOWS_DELETE_ERROR
} from './types'

export const loadShow = () => action(SHOWS_LOAD)
export const loadShowSuccess = (data) => action(SHOWS_LOAD_SUCCESS, { data })
export const loadShowFailure = () => action(SHOWS_LOAD_ERROR)

export const setValue = (payload) => action(SET_VALUE, { payload })

export const createShow = (data) => action(SHOWS_CREATE, { data })
export const createShowSuccess = (data) => action(SHOWS_SUCCESS, { data })
export const createShowFailure = () => action(SHOWS_ERROR)

export const updateShow = (data) => action(SHOWS_UPDATE, { data })
export const updateShowSuccess = (data) => action(SHOWS_UPDATE_SUCCESS, { data })
export const updateShowFailure = () => action(SHOWS_UPDATE_ERROR)

export const deleteShow = (data) => action(SHOWS_DELETE, { data })
export const deleteShowSuccess = (data) => action(SHOWS_DELETE_SUCCESS, { data })
export const deleteShowFailure = () => action(SHOWS_DELETE_ERROR)